:root {
    --size: 14rem
}

body {
  font: 3rem "Century Gothic", Futura, sans-serif;
  color: #47d7df;
  margin: 2rem;
}
/* 
ol, ul {
  padding-left: 50px;
} */

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1rem solid #19dabd;
  color: #4793df;
  float: left;
  font-size: 50px;
  line-height: var(--size);
  height: var(--size);
  margin-right: -1rem;
  margin-top: -1rem;
  padding: 0;
  text-align: center;
  width: var(--size);
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game-info {
  margin-left: 2rem;
  position: absolute;

}

.game-board {
    display: flex;
    position: absolute;
    left: 50%;
    margin-left: -18%;
    top: 50%;
    margin-top: -18%;
    border-radius: 50%;
}